@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500&display=swap);
.cart__section {
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* SHOPPING CART */

.shopping__cart {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
  color: var(--main-color);
}

.shopping__cart-header {
  padding: 10px 0;
}

.shopping__cart-block {
  padding: 10px 20px;
  box-shadow: 1px 1px 8px 0 #00000026;
}

.shopping__cart-list {
  list-style: none;
  display: flex;
  margin: 10px 0 30px 0;
  font-weight: 600;
}

.prod,
.cart__product-info {
  width: 50%;
}

.qty,
.cart__product-quantity {
  width: 20%;
  text-align: center;
}

.total,
.cart__product-total {
  width: 20%;
  text-align: center;
}

.cart__product-remove {
  width: 10%;
  text-align: center;
  color: #a7a7a7;
  cursor: pointer;
  font-size: 1.4rem;
}

/* CART BOTTOM */

.cart__product-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart__back {
  width: 100px;
  margin: 20px 10px;
  color: #0069c4;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.cart__arrow {
  font-size: 1.5rem;
}

.total__sum {
  font-weight: 700;
  font-size: 1.4rem;
  margin-right: 20px;
}


/* EMPRY CART */

.empty__cart {
  width: 40%;
  text-align: center;
}

.empty__cart-title {
  font-weight: 500;
  color: var(--main-color);
  font-size: 1.2rem;
  margin-top: 20px;
}


/* CART PRODUCT */

.cart__product {
    display: flex;
    width: 100%;
    align-items: center;
    border-bottom: 1 px solid #dadada;
  }
  
  .cart__border {
    border-bottom: 1px solid #dadada;
  }
  
  .cart__product-info {
    display: flex;
    align-items: center;
  }
  
  .cart__product-name {
    font-size: 1.1rem;
    padding: 0 20px;
  }
  
  .cart__product-name span {
    color: #848484;
    font-weight: 500;
  }
  
  .cart__product-image {
    width: 150px;
    height: 150px;
    object-fit: cover;
  }
  
  .cart__product-quantity {
    display: flex;
    justify-content: center;
  }
  
  .cart__product-input {
    width: 25%;
    text-align: center;
    padding: 5px 0;
    font-size: 1rem;
    border: 1px solid #a9a9a9;
  }
  
  .cart__product-button {
    padding: 0 10px;
    cursor: pointer;
  }
  
  .cart__product-discount {
    display: flex;
    flex-direction: column;
  }
  
  .discount-text {
    background: #f44336;
    width: 50%;
    color: #fff;
    margin: 0 auto;
  }
/* HEADER */

.header {
  padding: 5px 0;
  display: flex;
  justify-content: end;
  box-shadow: 2px 1px 5px 0 #00000017;
}

.header__cart-block {
  position: relative;
  width: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  margin-right: 30px;
  cursor: pointer;
}

.header__cart-icon {
  width: 40px;
}

.header__cart-quantity {
  position: absolute;
  bottom: 0;
  right: 15px;
  background-color: #02b852;
  border-radius: 50%;
  color: #fcfcfc;
  font-weight: 700;
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
}

/* PRODUCT SECTION */

.product__section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 80%;
  margin: 50px auto;
}

.product__card {
  flex-basis: 300px;
  box-shadow: 0px 1px 9px 0 #0000002e;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
}

.product__card:hover {
  box-shadow: 0px 1px 9px 0 #00000075;
}

.product__card-image {
  width: 100%;
  height: 70%;
  object-fit: contain;
}

.product__card-name {
  text-align: center;
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: 3px;
  color: var(--main-color);
}

.product__card-price {
  text-align: center;
  padding: 10px;
  font-size: 1.4rem;
  font-weight: 600;
  color: #ff9c0b;
}

.product__card-add {
  width: 100%;
  padding: 10px 0;
  text-align: center;
  background-color: #ffb42e;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
}

.product__card-discount {
  background-color: red;
  color: #fff;
  text-align: center;
  position: absolute;
  left: 210px;
  right: 0;
  top: 20px;
  transform: rotateZ(45deg);
  width: 115px;
}

* {
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

:root {
  --main-color: #262435;
}

a {
  text-decoration: none;
}

@media screen and (max-width: 650px) {
  .shopping__cart-list {
    display: none;
  }

  .cart__product {
    flex-direction: column;
  }

  .cart__product-info {
    flex-direction: column-reverse;
  }

  .cart__product-info {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }

  .cart__product-quantity {
    width: 50%;
  }

  .product__section {
    justify-content: center;
  }

}

